@import "variable";

h1 {
  line-height: 3rem;
}
.bold-text {
  font-weight: bold;
  color: $color-dark;
}

main {
  margin-top: 44px;
  .container {
    width: 100%;
    max-width: 1590px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    padding-left: 22px;
    padding-right: 22px;
  }

  .container-fluid {
    width: 100%;
    // max-width: 1590px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    // padding-left: 22px;
    // padding-right: 22px;
  }
}
.chat-and-store {
  .container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .shop-header {
      font-size: 3rem;
      flex-basis: 1;
      flex-grow: 1;
      span {
        color: $color-dark;
      }
    }

    .shop-chat {
      flex-basis: 1;
      flex-grow: 1;
      .wrapper {
        display: flex;
        align-items: center;
        justify-content: end;
        .chat-icon {
          display: inline-block;
          width: 40px;
          height: 40px;
          border-radius: 20px;
          overflow: hidden;
          * {
            width: 100%;
            height: 100%;
          }
        }
        .text {
          display: flex;
          flex-direction: column;
          margin-left: 10px;
        }
      }
    }
  }
}

@media screen and (max-width: 833px) {
  .chat-and-store {
    .container {
      display: flex;
      flex-direction: column;

      .shop-chat {
        margin-top: 44px;
        .wrapper {
          justify-content: start;
        }
      }
    }
  }
}

.productnav-section {
  padding-bottom: 62px;
  .product-card-container {
    display: flex;
    gap: 10px;
    width: 100%;
    box-sizing: border-box;
    overflow-x: scroll;
    scrollbar-width: none;
    padding: 10px 22px 40px;
    .product-card-item {
      height: 148px;
      min-width: 136px;
      padding: 18px 8px 16px;
      border-radius: 18px;
      overflow: hidden;
      .card-image {
        padding-bottom: 16px;
        img {
          display: block;
          max-height: 78px;
          width: auto;
          margin: 0 auto;
        }
      }
      .card-info {
        text-align: center;
        white-space: nowrap;
        a {
          font-size: 0.75rem;
          color: $color-dark;
          font-weight: 600;
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}

.cardsshelf-section {
  padding-bottom: 60px;
  .cardsshelf-header {
    font-size: 1.75rem;
    padding-bottom: 14px;
  }

  .cardsshelf-container {
    display: flex;
    gap: 20px;
    overflow-y: scroll;
    scrollbar-width: none;
    padding: 10px 22px 40px;
    width: 100%;
    box-sizing: border-box;

    .product-card-item {
      border-radius: 18px;
      overflow: hidden;
      min-width: 400px;
      height: 500px;
      box-shadow: 0 5 10 0 rgba(#000, 0.1);
      transition: all 0.3s ease-out;

      &.card-size-little {
        min-width: 320px;
        height: 230px;

        .card-content-header-upper {
          svg {
            width: 3rem;
          }
        }

        .card-content-header-lower {
          a {
            font-size: 1.5rem !important;
          }
        }
      }

      &.card-size-small {
        min-width: 320px;
        max-width: 320px;
        height: 500px;
      }

      &.card-size-medium {
        min-width: 400px;
        max-width: 400px;
        height: 500px;
      }

      &.card-size-large {
        min-width: 480px;
        max-width: 480px;
        height: 500px;
      }

      &:hover {
        transform: scale(1.01);
      }
      .card-wrapper {
        width: 100%;
        height: 100%;
        position: relative;
        .card-image-full {
          width: 100%;
          height: 100%;

          img {
            width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
          }
        }

        .card-image-top {
          width: 100%;
          padding: 30px;
          img {
            width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
          }
        }

        .card-info-top {
          position: absolute;
          padding: 30px;
          // width: 340px;
          width: 100%;
          box-sizing: unset;
          inset-inline-start: 0;
          top: 0;
          box-sizing: border-box;
        }

        .card-info-bottom {
          position: absolute;
          padding: 30px;
          width: 100%;
          box-sizing: unset;
          inset-inline-start: 0;
          bottom: 0;
          box-sizing: border-box;
        }

        .card-content-header-upper {
          margin-bottom: 0.4rem;
          font-size: 12px;
        }

        .card-content-header-lower {
          a {
            font-size: 28px;
            color: $color-dark;
            text-decoration: none;
          }
        }

        .card-content-desc {
          font-size: 1rem;
          padding-top: 10px;
        }
      }

      .card-wrapper.card-darkbg {
        .card-info-top,
        .card-info-bottom {
          color: $color-white;
          .card-content-header-lower {
            a {
              color: $color-white;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 833px) {
  .cardsshelf-section {
    padding-bottom: 2rem;
    .cardsshelf-header {
      font-size: 1.5rem;
    }

    .cardsshelf-container {
      gap: 10px;
      padding: 10px 22px 40px;

      .product-card-item {
        min-width: 310px !important;
        height: 450px !important;

        &.card-size-little {
          min-width: 320px !important;
          height: 230px !important;

          .card-content-header-upper {
            svg {
              width: 3rem;
            }
          }
        }

        .card-wrapper {
          .card-image-top {
            padding: 20px;
          }

          .card-info-top {
            padding: 20px;
          }

          .card-info-bottom {
            padding: 20px;
          }

          .card-content-header-upper {
            margin-bottom: 0.4rem;
            font-size: 12px;
          }

          .card-content-header-lower {
            a {
              font-size: 18px;
            }
          }
        }
      }
    }
  }
}

.quick-link-section {
  padding-bottom: 60px;
  .quick-link-header {
    font-size: 2rem;
    color: green;
    padding-bottom: 14px;
  }

  .quick-link-body {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    button {
      padding: 10px 20px;
      font-size: 1rem;
      color: $color-dark;
      border: 1px solid $color-dark;
      border-radius: 18px;
      background: none;
    }
  }
}
